.project {
  width: 100%;
  margin-top: 50px;
  height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project h1 {
  margin-top: 50px;
  color: #3e497a;
  font-size: 35px;
}
.project img {
  width: 700px;
  border-radius: 10px;
}

.project p {
  font-size: 30px;
  color: #3e497a;
  margin-bottom: 0.1%;
}

.project svg {
  margin-top: 60%;
  font-size: 60px;
  color: #3e497a;
}

.description {
  width: 50%;
}

.githubLink {
  cursor: pointer;
}
